<template>
  <v-card
    class="mx-auto my-12 search-box"
    max-width="374"
    style="margin-top: 5px !important"
  >
    <v-sheet elevation="6">
      <v-toolbar color="primary" dark>
        <v-toolbar-title>{{
          infosDistrito_ ? infosDistrito_ + ", Perú" : ""
        }}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="isFormBusqueda()">
          <v-icon>mdi-magnify</v-icon>
        </v-btn>
      </v-toolbar>
      <div
        style="
          display: inline-grid;
          text-align: center;
          margin: 9px 0px 9px 9px;
        "
      >
        <v-btn class="mx-2" fab small @click="isClickTabs('notary')">
          <v-img
            :src="require('../assets/icons/map_menu/icon_notarpa.svg')"
            height="height: 100%;"
            contain
          />
        </v-btn>
        <span style="font-size: 9px; margin-top: 5px">Notaria</span>
      </div>

      <div
        style="
          display: inline-grid;
          text-align: center;
          margin: 9px 0px 9px 9px;
        "
      >
        <v-btn class="mx-2" fab small @click="isClickTabs('press')">
          <v-img
            :src="require('../assets/icons/map_menu/icon_rrpp.svg')"
            height="height: 100%;"
            contain
          />
        </v-btn>
        <span style="font-size: 9px; margin-top: 5px">RR.PP.</span>
      </div>
      <div
        style="
          display: inline-grid;
          text-align: center;
          margin: 9px 0px 9px 9px;
        "
      >
        <v-btn class="mx-2" fab small @click="isClickTabs('court')">
          <v-img
            :src="require('../assets/icons/map_menu/icon_ppjj.svg')"
            height="height: 100%;"
            contain
          />
        </v-btn>
        <span style="font-size: 9px; margin-top: 5px">PP.JJ.</span>
      </div>

      <div
        style="
          display: inline-grid;
          text-align: center;
          margin: 9px 0px 9px 9px;
        "
      >
        <v-btn class="mx-2" fab small @click="isClickTabs('police')">
          <v-img
            :src="require('../assets/icons/map_menu/icon_comisaria.svg')"
            height="height: 100%;"
            contain
          />
        </v-btn>
        <span style="font-size: 9px; margin-top: 5px">Comisarias</span>
      </div>
      <div
        style="
          display: inline-grid;
          text-align: center;
          margin: 9px 0px 9px 9px;
        "
      >
        <v-btn class="mx-2" fab small @click="isClickTabs('municipality')">
          <v-img
            :src="require('../assets/icons/map_menu/icon_municipio.svg')"
            height="height: 100%;"
            contain
          />
        </v-btn>
        <span style="font-size: 9px; margin-top: 5px">Municipios</span>
      </div>
    </v-sheet>

    <v-card-text v-if="isPanelMain">
      <v-row align="center" align-content="center">
        <v-col cols="12">
          <v-list dense v-if="isListEntidad">
            <v-list-item-group v-model="selectedItem" color="primary">
              <v-list-item
                v-for="(item, i) in infoListDirections"
                :key="i"
                :style="
                  'display:' + (item.directions_kilometraje <= 35 ? '' : 'none')
                "
              >
                <v-list-item-icon>
                  <v-icon>mdi-checkbox-multiple-blank-circle-outline</v-icon>
                </v-list-item-icon>
                <v-list-item-icon :style="'margin-right: 15px;'">
                  {{
                    (item.directions_kilometraje == 0
                      ? "0.00"
                      : item.directions_kilometraje) + " Km"
                  }}
                  <span style="margin-left: 12px"> | </span>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-list-item-title
                        v-bind="attrs"
                        v-on="on"
                        v-text="item.directions_name"
                        @click="isDetalleDescripcion(item)"
                      ></v-list-item-title>
                    </template>
                    <span>{{ item.directions_name }}</span>
                  </v-tooltip>
                </v-list-item-content>
              </v-list-item>
              <v-divider
                style="border-top-color: #1976d2; border-top-style: dotted"
                class="mx-4"
              ></v-divider>
            </v-list-item-group>
          </v-list>
          <div v-if="isDescription">
            <v-card-title style="font-size: 16px;font-weight: bold; color: #003f6b">{{
              detalle_.directions_name
            }}</v-card-title>
            <v-card-text>
              <div class="text-subtitle-2">
                {{ detalle_.directions }}
              </div>
            </v-card-text>
            <v-divider class="mx-4" style="border-top-color: #1976d2; border-top-style: dotted"></v-divider>

            <v-list dense>
              <v-list-item-group color="primary">
                <v-list-item>
                  <v-list-item-icon>
                    <v-icon>mdi-phone-in-talk</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title>
                      {{
                        detalle_.directions_phone
                          ? detalle_.directions_phone
                          : "No registrado"
                      }}
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-icon>
                    <v-icon>mdi-av-timer</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      >{{
                        detalle_.directions_scheduler
                          ? detalle_.directions_scheduler
                          : "No registrado"
                      }}
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-icon>
                    <v-icon>mdi-airplane-takeoff</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title
                      >{{
                        detalle_.directions_kilometraje
                          ? detalle_.directions_kilometraje + " Km"
                          : "No registrado"
                      }}
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>

            <v-divider class="mx-4" style="border-top-color: #1976d2; border-top-style: dotted"></v-divider>

            <v-card-title>¿Qué deseas hacer?</v-card-title>

            <v-card-text style="text-align: center;">
                  <v-btn
               
        color="primary"
        dark
                    :href="
                      'https://www.google.com/maps/dir/' +
                      detalle_.directions_from +
                      '/' +
                      detalle_.directions_to +
                      '/am=t'
                    "
                    target="_blank"
                  >
                    <v-icon>mdi-map</v-icon> <span style="text-transform: initial">Llévame con google maps</span>
                  </v-btn>
             
           
            </v-card-text>

            <v-card-actions style="display: grid !important;">
              <v-btn color="primary lighten-2" text @click="isBack()">
                <v-icon>mdi-chevron-double-left</v-icon> Regresar
              </v-btn>
            </v-card-actions>
          </div>
          <v-col cols="12" v-if="isFormulario" style="text-align: center">
            <v-select
              v-model="form.type"
              :items="options.types"
              label="Tipo de Institucion"
            ></v-select>
            <v-select
              v-model="form.department_id"
              :items="options.departments"
              item-text="name"
              item-value="id"
              label="Departamento"
              @change="getProvinces"
              no-data-text="No hay datos disponibles"
            ></v-select>
            <v-select
              v-model="form.province_id"
              :items="options.provinces"
              item-text="name"
              item-value="id"
              label="Provincia"
              @change="getDistricts"
              no-data-text="No hay datos disponibles"
            ></v-select>
            <v-select
              v-model="form.district_id"
              :items="options.districts"
              item-text="name"
              item-value="id"
              label="Distrito"
              @change="getSites"
              no-data-text="No hay datos disponibles"
            ></v-select>

            <v-btn
              :disabled="!searchEnabled"
              color="primary"
              rounded
              width="200"
              @click="onSearch"
              >Buscar</v-btn
            >
          </v-col>
        </v-col>
      </v-row>
    </v-card-text>

    <div style="text-align: center; margin: 9px 0px 9px 9px">
      <v-btn
        rounded
        color="primary"
        dark
        v-if="isPanelMain"
        @click="isHideShowList()"
      >
        <label style="margin-right: 10px; text-transform: initial"
          >Ver en mapa</label
        >
        <v-icon>mdi-arrow-up-bold-circle</v-icon>
      </v-btn>
      <v-btn
        rounded
        color="primary"
        dark
        v-if="!isPanelMain"
        @click="isHideShowList()"
      >
        <label style="margin-right: 10px; text-transform: initial"
          >Ver en mapa</label
        >
        <v-icon>mdi-arrow-right-bold-circle</v-icon>
      </v-btn>
    </div>
  </v-card>
</template>

<script>
import axios from "axios";

export default {
  props: ["infoListDirections", "infosDistrito_", "myProp"],
  watch: {
    myProp: function () {
      this.isDetalleDescripcion(this.myProp);
    },
  },

  data: () => ({
    tab: null,
    items: [
      { tab: "notary", content: "Notaria" },
      { tab: "press", content: "RRPP" },
      { tab: "court", content: "Poder Judicial" },
      { tab: "police", content: "Comisaria" },
      { tab: "municipality", content: "Municipalidad" },
    ],

    selectedItem: 1,
    itemsx: [
      { text: "Real-Time", icon: "mdi-clock" },
      { text: "Audience", icon: "mdi-account" },
      { text: "Conversions", icon: "mdi-flag" },
    ],

    isLoading: false,
    //PANELES DE TIPOS DE SERVICIOS
    isPanelMain: true,
    isListEntidad: true,
    isSearching1: false,
    isSearching2: false,
    isSearching3: false,
    isSearching4: false,
    isSearching5: false,
    isFormulario: false,
    isSearching5_2: false,
    //CARD DE DETALLE E IR AL MAPA// POBLADO DE LAS TARJETAS

    isCardDetails_1: false,
    isCardDetails_2: false,
    isCardDetails_3: false,
    isCardDetails_4: false,
    isCardDetails_5: false,
    isCardDetails_5_1: false,
    isCardDetails_5_2: false,
    isDescription: false,

    isHideShowList_: true,

    form: {
      type: "",
      department_id: "",
      province_id: "",
      district_id: "",
      site_id: "",
    },
    //DETALLE PARA EL CARD
    detalle_: {
      directions_from: "",
      directions_to: "",
      directions: "",
      directions_phone: "",
      directions_scheduler: "",
      directions_name: "",
    },

    options: {
      types: [
        { value: "notary", text: "Notaria" },
        { value: "press", text: "RRPP" },
        { value: "court", text: "Poder Judicial" },
        { value: "police", text: "Comisaria" },
        { value: "municipality", text: "Municipalidad" },
      ],
      departments: [],
      provinces: [],
      districts: [],
      sites: [],
    },
    results: [],
  }),
  computed: {
    searchEnabled() {
      return true;
      //return this.form.site_id > 0
    },
  },
  mounted() {
    this.getDepartments();
  },
  methods: {
    getDepartments() {
      let url = "ubigeo/departments";
      axios
        .get(url)
        .then((response) => {
          this.options.departments = response.data;
        })
        .catch()
        .then(() => {});
    },
    getProvinces(departmentId) {
      let url = "ubigeo/provinces?department_id=" + departmentId;
      axios
        .get(url)
        .then((response) => {
          this.options.provinces = response.data;
        })
        .catch()
        .then(() => {});
    },
    getDistricts(provinceId) {
      let url = "ubigeo/districts?province_id=" + provinceId;
      axios
        .get(url)
        .then((response) => {
          this.options.districts = response.data;
        })
        .catch()
        .then(() => {});
    },
    getSites(districtId) {
      let url = "sites?district_id=" + districtId + "&type=" + this.form.type;
      axios
        .get(url)
        .then((response) => {
          this.options.sites = response.data;
        })
        .catch()
        .then(() => {});
    },

    isClickTabs(data_) {
      //CAMBIO DE TAB DESPLIEGUE : 1
      this.isPanelMain = true;
      this.isListEntidad = true;
      this.isDescription = false;
      this.isFormulario = false;

      let loc_ = {};
      loc_.type = data_;
      this.$emit("typeEntidad", loc_);
    },
    isBack() {
      this.isPanelMain = true;
      this.isListEntidad = true;
      this.isDescription = false;
      this.isFormulario = false;
    },

    //POBLADO PARA LAS TARJETAS
    isDetalleDescripcion(data_) {
      this.isPanelMain = true;
      this.isListEntidad = false;
      this.isDescription = true;
      this.isFormulario = false;
      this._isCardDetails_Poblar(data_);
    },

    isFormBusqueda() {
      this.isPanelMain = true;
      this.isListEntidad = false;
      this.isFormulario = true;
      this.isDescription = false;
    },

    onSearch() {
      this.isPanelMain = true;
      this.isListEntidad = true;
      this.isFormulario = false;
      this.isDescription = false;
      this.$emit("onSearch", this.form);
    },

    //POBLADO PARA LAS TARJETAS
    _isCardDetails_Poblar(data_) {
      this.detalle_.directions_from = data_.directions_from;
      this.detalle_.directions_to = data_.directions_to;
      this.detalle_.directions = data_.directions;
      this.detalle_.directions_phone = data_.directions_phone;
      this.detalle_.directions_scheduler = data_.directions_scheduler;
      this.detalle_.directions_name = data_.directions_name;
      this.detalle_.directions_kilometraje = data_.directions_kilometraje;
    },

    isHideShowList() {
      this.isPanelMain = !this.isPanelMain;
    },
  },
};
</script>

<style lang="scss">
#map {
  height: calc(100vh - 56px);
}
</style>

<style lang="scss" scoped>
.search-box {
  position: absolute;
  background-color: white;
  border-radius: 0.5rem;
  left: 0.5rem;
  top: 0.5rem;
  width: calc(100% - 1rem);
  max-width: 300px;
  margin: 0 !important;
}
</style>